const DarkModePicker = () => {
  return (
    <></>
  )
}



const TopNavigationBar = () => {
  const {
    connectButtonStyle,
    connectButtonText,
    selectedPage,
    draggable,
    setDraggable,
    darkMode,
    setDarkMode,
    language,
    setLanguage,
    connected,
    setAddFormulaOpen,
  } = useDeviceContext();
  const router = useRouter();

  const [isOpen, setIsOpen] = useState(false);
  const { t } = useTranslation()
  const {
    formulas, setFormulas, layout, setLayout
  } = useDeviceContext()

  async function addRandomFormula() {
    const formulaId = uuid()
    const randomColors = ["#f44336", "#e91e63", "#9c27b0", "#673ab7", "#3f51b5", "#2196f3", "#03a9f4", "#00bcd4", "#009688", "#4caf50", "#8bc34a", "#cddc39", "#ffeb3b", "#ffc107", "#ff9800", "#ff5722", "#795548", "#607d8b"]
    const color = randomColors[Math.floor(Math.random() * randomColors.length)]

    await actuallyAddFormula({
      color: color,
      formulaId: formulaId
    })

  }
  async function actuallyAddFormula(pack: any) {
    console.log('actuallyAddFormula.input', pack)
    const newId = uuid()

    await setLayout(
      produce(layout, draft => {
        draft["md"]?.push({ i: newId, x: 0, y: 0, w: 1, h: 1 });
        draft["xs"]?.push({ i: newId, x: 0, y: 0, w: 1, h: 1 });
        draft["sm"]?.push({ i: newId, x: 0, y: 0, w: 1, h: 1 });

      })
    )

    await setFormulas(
      produce(formulas, draft => {
        var formula = {
          id: newId,
          title: "",
          text: "",
          color: "gray",
          backdrop: "gray", // deprecated
          formulaType: EnumFormulaType.Espresso,
          steps: [],
          buildupDuration: 0,
          lossyWeight: 0,
          maximumDuration: 0,
          isShared: false,
          category: "",
          formulaId: ""
        }
        Object.assign(formula, pack);
        formula.id = newId // hate this
        formula.formulaId = newId
        formula.isShared = false // duh :)
        console.log('actuallyAddFormula.output', formula)

        draft[formula.formulaId] = formula
        return draft

      })
    )

  }

  const [communityModal, setCommunityModal] = useState(false)
  const [tokenModal, setTokenModal] = useState(false)
  const [shareToken, setShareToken] = useState<string>("")
  const [searchToken, setSearchToken] = useState<string>("")
  const [searchSuggestions, setSearchSuggestions] = useState<any[]>([])
  const searchTimeout = useRef<ReturnType<typeof setTimeout>>()

  const fns = [
    {
      key: "community",
      onClick: () => {
        setCommunityModal(true)
      },
      title: "From Community",
      text: "Explore the shared formula from people around the world !",
      animationData: lottieEarth,
    },

    {
      key: "token",
      onClick: () => {
        setShareToken("")
        setTokenModal(true)
      },
      title: "From Token",
      text: "Load from a SHARE code!",
      animationData: lottieLotus,
    },

    {
      key: "invent",
      onClick: async () => {
        // setAddFormulaOpen(true)

        // just invent by add a new shit in, let user edit inside lab view, smart

        await addRandomFormula()
      },
      title: "Let's Invent",
      text: "Create new formula now !",
      animationData: lottieCoffee,
    },

  ]


  const imageButtons = fns.map((fn) => (
    <div
      key={fn.key}
      onClick={() => {
        setIsOpen(false)
        fn.onClick()
      }}
      className="flex items-center justify-between h-20 w-full bg-gray-100 dark:bg-slate-800 dark:text-white hover:bg-blue-500 text-black  hover:text-white py-2 px-4  hover:border-transparent rounded-xl "
    >
      {/* <img src="/path/to/image.png" alt="Icon" className="w-8 h-8" /> */}
      <Lottie
        options={{
          loop: true,
          autoplay: true,
          animationData: fn.animationData,
          rendererSettings: {
            preserveAspectRatio: 'xMidYMid slice'
          }
        }}
        height={80}
        width={80}
      />

      <span className="flex-1 text-left ml-4">
        <div className="text-lg font-bold">{fn.title}</div>
        <div className="text-sm ">{fn.text}</div>
      </span>
    </div>
  ))


  return (
    <>
      {tokenModal && (
        <Modal
          size="small"
          title="Load from Token"
          onClose={() => setTokenModal(false)}
          primaryAction={{
            label: "Load",
            onPress: () => {
              setTimeout(async () => {
                const request = await fetch(`https://api.anatalab.com/gateway/rosario/get_token/${shareToken}`)
                if (request.status != 200) {
                  return
                }

                const body = await request.json()
                console.log("Formula is", body.formulas)

                // create a new formula from this
                await actuallyAddFormula(body.formulas)


              })
              setTokenModal(false)
            }
          }}
          secondaryAction={{
            label: "Cancel",
            onPress: () => {
              setTokenModal(false)
            }
          }}
        >
          <TextField
            name="Token"
            label="Share Token"
            placeholder=""
            value={shareToken || ""}
            onChange={(e) => setShareToken(e)}
          />
        </Modal>
      )}

      {communityModal && (
        <Modal
          size="small"
          title="Explore the community"
          onClose={() => setCommunityModal(false)}
          primaryAction={{
            label: "Load",
            onPress: () => {
              setCommunityModal(false)
            }
          }}
          secondaryAction={{
            label: "Cancel",
            onPress: () => {
              setCommunityModal(false)
            }
          }}
        >
          <TextField
            name="Keyword"
            label="Keyword"
            placeholder=""
            value={searchToken || ""}
            onChange={(e) => {
              setSearchToken(e)

              clearTimeout(searchTimeout.current)
              searchTimeout.current = setTimeout(async () => {
                const url = new URL(`https://api.anatalab.com/gateway/rosario/search_formula`)
                url.searchParams.append('title', searchToken)
                const request = await fetch(url)
                if (request.status != 200) {
                  return
                }

                const body = await request.json()
                console.log("Suggestion is", body)
                setSearchSuggestions(body.slice(0, 5))
              }, 100)


              // convert to suggestion for rendering



            }}
          />

          <div className="flex flex-col flex-wrap gap-4 mt-4">
            {searchSuggestions.map((suggestion) => {
              return (
                <motion.div
                  key={suggestion.id}
                  className="h-12 w-auto rounded-lg flex flex-row items-center justify-between select-pointer cursor-pointer"
                  style={{
                    background: suggestion.color
                  }}
                  onClick={async () => {
                    await actuallyAddFormula(suggestion)
                    console.log("add", suggestion)
                    setCommunityModal(false)
                  }}
                  initial={{ x: 100, opacity: 0 }}
                  animate={{ x: 0, opacity: 1 }}
                  transition={{ type: "spring", stiffness: 100 }}
                >
                  <h1 className="text-white font-mono m-2 ">{suggestion.title}</h1>
                  <h1 className="text-gray-300 font-mono m-2 ">({suggestion.steps.length} steps)</h1>

                </motion.div>
              )
            })}
          </div>

        </Modal>
      )}



      <div className="fixed z-10 flex w-full items-center justify-between bg-transparent p-2  cursor-pointer ">
        <div>
          <div
            className="logo overflow-hidden "
            onClick={() => {
              router.push("/");
            }}
          >
            {darkMode ? (
              <Image src={AnataLABLogoWhite} alt="AnataLAB Logo" width={80} />
            ) : (
              <Image src={AnataLABLogoBlack} alt="AnataLAB Logo" width={80} />
            )}
          </div>

              <DarkModePicker/>

            </div>


        {router.pathname != "/" && (
          <div className="flex h-[48px] flex-row items-center justify-evenly space-x-1">
            {router.pathname == "/rosario" && (
              <>
                {draggable && (
                  <Dialog>
                    <DialogTrigger>

                      <div
                        className={`flex h-[48px] w-[48px] cursor-pointer items-center  justify-center rounded-xl border border-dashed  p-1 border-black dark:border-white text-black dark:text-white hover:scale-105`}
                        onClick={() => {
                          // setDraggable(!draggable);
                          setIsOpen(true)

                        }}
                      >
                        <FaPlus className="" />
                      </div>

                    </DialogTrigger>
                    <DialogContent>
                      <DialogHeader>
                        <DialogDescription>
                        </DialogDescription>
                      </DialogHeader>
                      <DialogFooter>
                        <DialogClose >
                          <div className="flex flex-col space-y-6  rounded-xl w-fit">
                            {imageButtons}
                          </div>

                        </DialogClose>
                      </DialogFooter>
                    </DialogContent>
                  </Dialog>
                )}
                <div
                  // className={`flex h-[48px] w-[48px] cursor-pointer  items-center justify-center rounded-xl border border-black p-1 ${draggable ? "bg-black" : ""}`}

                  className={cn("flex size-[48px] cursor-pointer items-center justify-center rounded-xl border border-black dark:border-white p-1 text-black dark:text-white hover:scale-105",
                        draggable ? "text-white bg-black dark:bg-white dark:text-black" : ""
                  )}
                  onClick={() => {
                    setDraggable(!draggable);
                  }}
                >
                  {draggable ? (
                    <IoLockOpenOutline  className=""/>
                  ) : (
                    <IoLockClosedOutline  className=""/>
                  )}
                </div>
              </>
            )}

            <div
              className={`flex h-[48px] w-fit cursor-pointer flex-row items-center justify-center space-x-2 rounded-xl border border-black p-3 dark:border-white text-black dark:text-white hover:scale-105`}
              onClick={() => {
                handleConnectButton();
              }}
            >
              {connected ? (
                <MdBluetoothConnected className="" />
              ) : (
                <MdBluetooth className="" />
              )}
              <h1
                // className={`${connected ? "text-white" : "text-black"
                //   } select-none text-black`}
                className="text-black dark:text-white"
              >
                {connectButtonText}
              </h1>
            </div>

          </div>
        )}


        {router.pathname == '/' && (
          <button
            className={cn(
              "flex flex-row h-[48px] w-fit cursor-pointer  items-center justify-center space-x-2 rounded-xl border border-black p-3 dark:border-white text-black dark:text-white",
              darkMode ? "border-white bg-black text-white" : "border-black bg-white"
            )}
            onClick={() => router.push("/rosario")}
          >
            <h1 className={cn(
              "text-black dark:text-white",
            )}>Go to Rosario</h1>
            <IoArrowForwardOutline
              className="group-hover:scale-150 transition-all duration-300"
            />
          </button>
        )}


      </div>
    </>);
};
export default TopNavigationBar;

